import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';
import { FontColor } from 'external/types/common';
import {
  withLinkMemo,
  LinkWrapperProps,
  DEFAULT_PROPS as DEFAULT_PROPS_BY_LINK
} from 'external/src/Link';
import { getStoryComponent } from 'shared/storyHelper';

import styles from './styles.scss';

const cd = cssDecorator(styles);

export type TitleFontWeight =
  'black'
  | 'bold'
  | 'semibold'
  | 'medium'
  | 'light'
  | 'extralight';

export type TitleVariant =
  'h1_extra_large'
  | 'h1_large'
  | 'h1_default'
  | 'h1_small'
  | 'h2_extra_large'
  | 'h2_large'
  | 'h2_default'
  | 'h2_small'
  | 'h3_default'
  | 'h4_default'
  | 'h5_default'
  | 'h6_default';

export type TagName =
  'span'
  | 'h1'
  | 'h2'
  | 'h3'
  | 'h4'
  | 'h5';

// Заготовка на случай расширения набора цветов для отдельных компонентов
export type TitleFontColor = FontColor;

const getTag = (variant: TitleVariant): string => {
  switch (variant) {
    case 'h2_default':
    case 'h2_small':
    case 'h2_large':
    case 'h2_extra_large':
      return 'h2';

    case 'h3_default':
      return 'h3';

    case 'h4_default':
      return 'h4';

    case 'h5_default':
      return 'h5';

    case 'h6_default':
      return 'h6';

    case 'h1_default':
    case 'h1_small':
    case 'h1_large':
    case 'h1_extra_large':
    default:
      return 'h1';
  }
};

export interface TitlePropsBase {
  /** Вариант заголовка */
  variant: TitleVariant;

  /** Имя тега */
  tagName?: TagName;

  /** Насыщенность шрифта */
  fontWeight?: TitleFontWeight;

  /** Цвет текста */
  color?: TitleFontColor;

  /** Коллбэк, вызываемый при нажатии на заголовок */
  onClick?: React.MouseEventHandler<HTMLElement>;

  /** Флаг для отрисовки строчного представления */
  inline?: boolean;

  /** ID для тестирования */
  dataTestId?: string;

  /** Дочерние элементы */
  children: React.ReactNode;

  /** Флаг для наследования цвета текста от родителя */
  inheritColor?: boolean;
}

const DEFAULT_PROPS = {
  color: 'primary',
  fontWeight: 'medium',
  inline: false,
  inheritColor: false
};

const Title: React.FC<TitlePropsBase> = ({
  variant,
  tagName,
  fontWeight = DEFAULT_PROPS.fontWeight,
  color = DEFAULT_PROPS.color,
  inline = DEFAULT_PROPS.inline,
  inheritColor = DEFAULT_PROPS.inheritColor,
  onClick,
  dataTestId,
  children
}) => {
  const titleClasses = cd(
    'title',
    {
      inline,
      [`font_weight_${ fontWeight }`]: true,
      [`variant_${ variant }`]: true,
      [`color_${ color }`]: !inheritColor
    }
  );

  return React.createElement(
    tagName || getTag(variant),
    {
      'data-test-id': dataTestId,
      onClick,
      className: titleClasses
    },
    children
  );
};

export const StoryComponent = getStoryComponent<TitlePropsBase, LinkWrapperProps>(
  DEFAULT_PROPS as TitlePropsBase,
  DEFAULT_PROPS_BY_LINK as LinkWrapperProps
);

export default withLinkMemo<TitlePropsBase>(Title);

export type TitleProps = TitlePropsBase & LinkWrapperProps;
